.rsl-large-desc {
    $ld: &;
    padding: 64px 0;
    
    @media (min-width: 1200px) {
        padding: 140px 0;
    }
    
    &__item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        gap: 30px;
        margin-bottom: math-clamp(64,140);
        
        &:last-child {
            margin-bottom: 0;
        }
        
        @media (min-width: 992px) {
            flex-direction: row-reverse;
            gap: math-clamp(30, 150);
            margin-bottom: 140px;
            
            &:last-child {
                margin-bottom: 0;
            }
        }
        
        &_reverse {
            flex-direction: row;
            
            #{$ld}__text {
                @media (min-width: 992px) {
                    flex-basis: 0;
                    flex-grow: 1;
                    max-width: 608px;
                }
            }
            
            #{$ld}__image {
                @media (min-width: 992px) {
                    flex-basis: 0;
                    flex-grow: 1;
                    max-width: 500px;
                    height: 440px;
                }
            }
        }
    }
    
    &__text {
        flex: 0 0 100%;
        max-width: 100%;
        
        @media (min-width: 992px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 490px;
        }
        
        &-wrap {
            max-width: 490px;
        }
        
        h2 {
            margin-bottom: math-clamp(30, 40);
            color: $black;
        }
    }
    
    &__image {
        overflow: hidden;
        border-radius: 8px;
        flex: 0 0 100%;
        max-width: 100%;
        height: 300px;
        
        @media (min-width: 992px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 608px;
            height: 405px;
        }
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
