.rsl-contact-us {
    position: relative;
    padding: 140px 0;
    
    &__title {
        display: flex;
        flex-direction: column;
        row-gap: 40px;
        
        h1 {
            color: $black;
            margin: 0;
            font-family: $font-family-denim;
        }
        
        p {
            margin: 0;
            font-size: 22px;
            line-height: 150%;
            color: $gray-600;
        }
    }
    
    &__content {
        position: relative;
        padding: 80px 0;
        margin-top: 80px;
        
        @media (min-width: 992px) {
            padding: 120px 0;
        }
        
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            border-radius: 0 0 16px 0;
            z-index: -10;
            background-color: #fff;
            
            @media (min-width: 1400px) {
                width: calc(100% - 128px);
            }
        }
    }
    
    &__form {
        display: flex;
        flex-wrap: wrap;
        gap: 24px;
        max-width: 1128px;
        width: 100%;
        justify-content: center;
    
        @media (min-width: 1200px) {
            justify-content: flex-start;
        }
    }
    
    &__form-wrap {
        position: relative;
        flex: 0 0 auto;
        width: 100%;
        max-width: 100%;
        
        @media (min-width: 992px) {
            max-width: 360px;
        }
        
        input:focus + label {
            top: 10px;
            left: 24px;
            font-size: 12px;
        }
        
        input:focus,
        input:active {
            padding-left: 24px;
            padding-top: 22px;
            padding-bottom: 10px;
        }
        
        input {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
            width: 100%;
            border-radius: 60px;
            border: 1px solid $black;
            background-color: $gray-100;
            padding: 16px 24px;
            color: $black;
            font-size: 16px;
            line-height: 24px;
        }
        
        label {
            position: absolute;
            color: $gray-600;
            font-size: 16px;
            left: 24px;
            top: 16px;
            transition: 0.5s all;
        }
        
        select {
            width: 100%;
            border-radius: 60px;
            border: 1px solid #283220;
            background-color: #f0edeb;
            padding: 16px 24px;
            color: #283220;
            font-size: 16px;
            line-height: 24px;
            outline: none;
        }
    }
    
    &__wrap-textarea {
        width: 100%;
        height: auto;
        position: relative;
        
        textarea {
            width: 100%;
            resize: none;
            border-radius: 30px;
            border: 1px solid $black;
            background-color: $gray-100;
            height: 270px;
            padding: 18px 24px;
            color: $black;
            font-size: 16px;
            line-height: 24px;
            outline: none;
            
            &:focus + label {
                top: 8px;
                left: 24px;
                font-size: 12px;
            }
        }
        
        label {
            position: absolute;
            left: 24px;
            top: 16px;
            color: $gray-600;
            transition: .5s all;
        }
    }
    
    &__sub-content {
        display: flex;
        flex-direction: column;
        row-gap: 24px;
        max-width: 682px;
    }
    
    &__desc {
        font-size: 10px;
        color: $gray-600;
        line-height: 160%;
        padding-top: 16px;
    }
    
    &__checkbox {
        position: relative;
        padding-left: 40px;
        width: 100%;
        
        input {
            appearance: none;
            position: absolute;
        }
        
        input:checked + label:before {
        
        }
        
        input:checked + label:after {
            opacity: 1;
        }
    
        label {
            font-size: 16px;
            line-height: 24px;
            color: $black;
        }
    
        label:before {
            content: '';
            display: block;
            position: absolute;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            border: 1px solid $black;
            top: 0;
            left: 0;
            background-color: transparent;
            z-index: 1;
        }
    
        label:after {
            content: '';
            display: block;
            position: absolute;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            border: 1px solid $black;
            top: 0;
            left: 0;
            background-color: $light-green;
            opacity: 0;
            z-index: 2;
        }
    
        p {
            color: $gray-600;
            font-size: 12px;
            line-height: 18px;
            margin: 0;
            padding-top: 8px;
        }
        
    }
}
