.rsl-product-highlights {
    padding: 60px 0;
    position: relative;
    
    @media (min-width: 992px) {
        padding: 120px 0;
    }
    
    &__title {
        margin: 0;
        color: $black;
        font-family: $font-family-denim;
    }
    
    &__wrap-items {
        padding-top: 80px;
        display: flex;
        flex-direction: column;
        position: relative;
        row-gap: 40px;
        
        @media (min-width: 992px) {
            row-gap: 80px;
        }
    }
    
    &__item {
        display: none;
        
        @media (min-width: 992px) {
            position: relative;
            max-width: 824px;
            width: 100%;
            background-color: $white;
            padding: 64px 78px 100px 192px;
            border: 1px solid #C9CCC7;
            box-shadow: 16px 16px 40px rgba(0, 0, 0, 0.04);
            border-radius: 16px;
            overflow: hidden;
            display: block;
        }
        
        &:before {
            content: '';
            position: absolute;
            width: 104px;
            height: 100%;
            background-color: $light-green;
            top: 0;
            left: 0;
        }
        
        &_reverse {
            margin-right: 0;
            margin-left: auto;
        }
    }
    
    &__icon {
        position: absolute;
        top: 64px;
        left: 64px;
        border: 1px solid $black;
        border-radius: 50%;
        background-color: $white;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80px;
        height: 80px;
    }
    
    &__content-wrap {
        display: flex;
        flex-direction: column;
        row-gap: 56px;
        
        h4 {
            color: $black;
            font-family: $font-family-denim;
            margin: 0;
        }
        
        p {
            margin: 0;
            color: $gray-600;
            line-height: 24px;
            padding-top: 16px;
        }
        
        h5 {
            color: $black;
            margin: 0;
            font-weight: 500;
        }
        
        ul {
            margin: 0;
            list-style-type: none;
            display: flex;
            flex-direction: column;
            row-gap: 10px;
            padding-top: 24px;
            
            li {
                position: relative;
                padding-left: 16px;
                color: $gray-600;
                font-size: 14px;
                line-height: 22px;
    
                &:before {
                    content: "";
                    position: absolute;
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background-color: $light-green;
                    left: 0;
                    top: 8px;
                }
            }
        }
    }
    
    //Mobile Item
    
    &__mobile-item {
        position: relative;
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
        overflow: hidden;
        border-radius: 16px;
        box-shadow: 16px 16px 40px rgba(0, 0, 0, 0.04);
        border: 1px solid #C9CCC7;
        background-color: $white;
        
        @media (min-width: 992px) {
            display: none;
        }
    }
    
    &__mobile-label {
        min-height: 64px;
        background-color: $light-green;
        width: 100%;
        position: relative;
    }
    
    &__mobile-icon {
        position: absolute;
        top: 24px;
        left: 40px;
        width: 64px;
        height: 64px;
        border: 1px solid $black;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
    }
    
    &__mobile-content-wrap {
        padding: 40px 40px;
        
        h4 {
            color: $black;
        }
        
        p {
            color: $gray-600;
            margin: 0;
            padding-top: 20px;
        }
        
        h5 {
            color: $black;
            padding-top: 40px;
            padding-bottom: 20px;
        }
        
        ul {
            list-style: none;
            margin: 0;
            
            li {
                position: relative;
                padding-left: 16px;
                color: $gray-600;
                font-size: 14px;
                margin-bottom: 14px;
                
                &:last-child {
                    margin-bottom: 0;
                }
    
                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 8px;
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background-color: $light-green;
                    
                }
            }
        }
    }
}
