.rsl-footer {
    background-color: $white;
    padding: 40px 0;
    position: relative;
    overflow: hidden;
    
    @media (min-width: 992px) {
        padding: 80px 0;
    }
    
    &__wrap {
        display: flex;
        flex-direction: column;
        column-gap: 80px;
    }
    
    &__info {
        display: flex;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        flex-direction: column;
        row-gap: 40px;
        
        @media (min-width: 992px) {
            flex-direction: row;
        }
    }
    
    //Left Block
    &__left {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        display: flex;
        flex-direction: column;
        row-gap: 40px;
        width: 100%;
    }
    
    &__address {
        font-size: 14px;
        line-height: 22px;
        color: $gray-600;
        margin: 0;
        max-width: 164px;
    }
    
    &__social,
    .social {
        display: flex;
        align-items: center;
        column-gap: 16px;
        
        &__icon {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            border: 1px solid $black;
            background-color: $light-green;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    
    //Right-Block
    
    &__right {
        display: flex;
        column-gap: 60px;
        flex-basis: 0;
        flex-grow: 2;
        max-width: 100%;
        
        @media (min-width: 576px) {
            column-gap: 126px;
        }
    }
    
    &__links {
        display: flex;
        row-gap: 16px;
        flex-direction: column;
        list-style: none;
        margin: 0;
        
        a {
            font-size: 18px;
            font-family: $font-family-denim;
            text-decoration: none;
            color: $black;
        }
    }
    
    
    //Privacy block
    
    &__privacy{
        font-size: 10px;
        display: flex;
        padding-top: 60px;
        flex-direction: column;
        row-gap: 24px;
        
        @media (min-width: 992px) {
            flex-direction: row;
            row-gap: 8px;
            padding-top: 104px;
        }
    }
    
    &__privacy-cover {
        display: flex;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        flex-direction: column;
        row-gap: 8px;
        width: 100%;
    }
    
    &__quote,
    &__desc {
        color: $gray-600;
    }
    
    &__desc {
        flex-basis: 0;
        flex-grow: 2;
        max-width: 100%;
    }
    
    &__privacy-links {
        display: flex;
        column-gap: 24px;
    
        a {
            text-decoration: none;
            color: $gray-600;
        }
    }
}
