.rsl-our-backers {
    padding: 64px 0;
    background-color: $white;
    
    @media (min-width: 1200px) {
        padding: 140px 0;
    }
    
    &__content,
    .content {
        display: flex;
        justify-content: space-between;
        
        @media (max-width: 1199px) {
            flex-direction: column;
            row-gap: 40px;
        }
        
        
        &__left,
        &__right {
            @media (max-width: 1199px) {
                flex-basis: 0;
                flex-grow: 1;
                max-width: 100%;
            }
        }
        
        &__left {
            max-width: math-clamp(330, 392);
            width: 100%;
            display: flex;
            flex-direction: column;
            row-gap: 40px;
            
            @media (max-width: 1199px) {
                max-width: 100%;
            }
            
            h2 {
                margin: 0;
                color: $black;
                font-family: $font-family-denim;
                font-weight: 400;
            }
            
            p {
                margin: 0;
                font-size: 18px;
                color: $gray-600;
            }
        }
        
        &__right {
            display: flex;
            column-gap: 40px;
            align-items: center;
            
            @media (max-width: 767px) {
                flex-direction: column;
                row-gap: 24px;
            }
        }
        
        &__logo-wrap {
            max-width: math-clamp(330, 284);
            width: 100%;
            height: auto;
            padding: 25px 60px;
            border: 1px solid $gray-100;
            box-shadow: 8px 16px 40px rgba(0, 0, 0, 0.04);
            border-radius: 8px;
            display: flex;
            justify-content: center;
    
            @media (max-width: 767px) {
                max-width: 100%;
                padding: 40px 84px;
            }
        }
    }
}
