.rsl-large-description-dark {
    padding: 64px 0;
    background-color: $army-green;
    
    @media (min-width: 1200px) {
        padding: 128px 0;
    }
    
    &__item {
        display: flex;
        flex-direction: column;
        row-gap: 24px;
        
        @media (min-width: 992px) {
            flex-direction: row;
            justify-content: space-between;
        }
    }
    
    &__title {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 466px;
        
        h2 {
            color: $white;
            margin: 0;
            font-family: $font-family-denim;
            text-transform: capitalize;
        }
    }
    
    &__subtitle {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 608px;
        display: flex;
        flex-direction: column;
        row-gap: 24px;
        
        @media (min-width: 768px) {
            row-gap: 48px;
        }
        
        p {
            font-size: math-clamp(16, 18);
            line-height: 160%;
            color: $gray-100;
            margin: 0;
        }
    }
}
