.rsl-market-appetite {
    background-color: $white;
    padding: 60px 0 40px 0;
    
    @media (min-width: 992px) {
        padding: 120px 0 70px 0;
    }
    
    &__wrap {
        display: flex;
        flex-direction: column;
        row-gap: 80px;
    }
    
    &__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @media (min-width: 768px) {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            flex-direction: row;
        }
    }
    
    &__title {
        color: $black;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        text-align: center;
        
        @media (min-width: 768px) {
            max-width: 592px;
            text-align: left;
        }
        
        h2{
            margin: 0;
            font-family: $font-family-denim;
        }
        
        p {
            color: #000;
            padding-top: 40px;
            font-size: math-clamp(18, 14);
        }
    }
    
    &__cards {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        align-items: center;
        row-gap: 40px;
        
        @media (min-width: 1300px) {
            flex-direction: row;
            align-items: flex-start;
        }
    }
    
    &__card-item {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 608px;
        
        h5 {
            padding: 40px 0;
        }
        
        svg {
            max-width: 100%;
            height: auto;
        }
    }
    
    &__item-description {
        display: flex;
        justify-content: space-between;
        column-gap: 40px;
        flex-direction: column;
        
        @media (min-width: 1200px) {
            flex-direction: row;
        }
    }
    
    &__text {
        
        width: 100%;
        
        &_left {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
        }
        
        &_right {
            flex-basis: 0;
            flex-grow: 2;
            max-width: 100%;
        }
    }
  
}
