.rsl-products-preview {
    position: relative;
    padding: 100px 0 40px 0;
    
    @media (min-width: 992px) {
        padding: 170px 0 60px 0;
    }
    
    &__wrap {
        display: flex;
        flex-direction: column-reverse;
        row-gap: 40px;
        
        @media (min-width: 992px) {
            justify-content: space-between;
            flex-direction: row;
        }
    }
    
    &__title {
        flex: 0 0 100%;
        max-width: 100%;
        
        @media (min-width: 992px) {
            flex: 0 0 50%;
            max-width: 608px;
        }
        
        h1 {
            font-family: $font-family-denim;
            margin: 0;
            color: $black;
        }
        
        p {
            font-size: math-clamp(16, 22);
            line-height: 150%;
            color: $gray-600;
            margin: 0;
            padding: 24px 0;
            
            @media (min-width: 992px) {
                padding: 40px 0;
            }
        }
    }
    
    &__image {
        flex: 0 0 100%;
        max-width: 100%;
        height: 100%;
        padding: 16px;
        background-color: #fff;
        border-radius: 16px;
        box-shadow: 16px 16px 40px rgb(0 0 0 / 4%);
        
        @media (min-width: 992px) {
            flex: 0 0 44%;
            max-width: 540px;
        }
        
        svg {
            width: 100%;
            height: auto;
            display: block;
        }
    }
}
