.rsl-next-gen {
    padding: 48px 0;
    position: relative;
    
    @media (min-width: 1200px) {
        padding: 140px 0;
    }
    
    .rsl-container {
        max-width: 100%;
        width: math-clamp(330px, 1256);
    }
    
    &__wrap {
        background-color: $white;
        border-radius: 16px;
        padding: 56px 32px;
        display: flex;
        flex-direction: column;
        row-gap: 34px;
        position: relative;
        z-index: 10;
        
        @media (min-width: 1200px) {
            padding: 90px;
            row-gap: 55px;
        }
    }
    
    &__info,
    .info {
        display: flex;
        flex-wrap: wrap-reverse;
        justify-content: center;
        row-gap: 48px;
        
        @media (min-width: 1200px) {
            display: flex;
            justify-content: space-between;
            
        }
        
        &__logo {
            max-width: math-clamp(250, 370);
            
            svg {
                width: 100%;
                height: auto;
            }
        }

        &__title {
            display: flex;
            flex-direction: column;
            row-gap: 30px;
            width: 100%;
            
            @media (min-width: 1200px) {
                max-width: 550px;
                row-gap: 40px;
            }

            h2 {

                color: $black;
                font-family: $font-family-denim;
                font-weight: 400;
            }

            p {
                margin: 0;
                color: $gray-600;
                line-height: 160%;
                font-weight: 400;
                font-size: math-clamp(16, 18);
            }
        }
    }

    &__items-wrap {
        display: flex;
        flex-direction: column;
        row-gap: 48px;
        
        @media (min-width: 1200px) {
            display: flex;
            justify-content: space-between;
            flex-direction: row;
        }

        .item {
            flex: 0 0 284px;
            max-width: 100%;
            display: flex;
            flex-direction: column;
            row-gap: 24px;
            
            @media (max-width: 1199px) {
                flex: 0 0 auto;
            }

            h5 {
                color: $black;
                font-weight: 500;
            }

            p {
                color: $gray-600;
                font-weight: 400;
                line-height: 22px;
                font-size: 14px;
                margin: 0;
            }
        }
    }
}
