.rsl-brokers-preview {
    padding: 100px 0;
    
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column-reverse;
        row-gap: 40px;
        
        @media (min-width: 992px) {
            flex-direction: row;
        }
    }
    
    &__title {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        
        @media (min-width: 768px) {
            max-width:500px;
        }
        
        h1 {
            font-family: $font-family-denim;
            margin: 0;
            color: $black;
        }
        
        p {
            color: $gray-600;
            margin: 0;
            font-size: math-clamp(16, 22)x;
            line-height: 150%;
            padding: 20px 0;
            
            @media (min-width: 768px) {
                padding: 40px 0;
            }
        }
    }
    
    &__collage {
        display: flex;
        flex-wrap: wrap;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        gap: 31px;
        position: relative;
        align-items: flex-end;
        height: auto;
        justify-content: center;
        
        @media (min-width: 768px) {
            flex-wrap: wrap;
            flex-basis: 0;
            flex-grow: 1;
            max-width: 580px;
            gap: 31px;
            position: relative;
            align-items: flex-end;
            height: auto;
            justify-content: space-between;
        }
    }
    
    &__collage-item {
        background-color: $white;
        padding: 10px;
        border-radius: 16px;
        overflow: hidden;
        box-shadow: 16px 16px 40px rgba(0, 0, 0, 0.04);
        
        @media (min-width: 768px) {
            flex-direction: column;
            
            &:nth-child(1) {
                max-width: 290px;
            }
    
            &:nth-child(2) {
                max-width: 260px;
            }
    
            &:nth-child(3) {
                max-width: 338px;
            }
        }
        
        img {
            max-width: 100%;
            width: 100%;
            height: auto;
        }
    }
    
    &__logo {
        position: absolute;
        left: 21px;
        bottom: 75px;
        width: 64px;
        height: 64px;
        border-radius: 50%;
        border: 1px solid $black;
        justify-content: center;
        align-items: center;
        background-color: $white;
        display: none;
        
        @media (min-width: 768px) {
            display: flex;
        }
    }
}
