.rsl-our-products {
    padding: 64px 0;
    background-color: $white;
    
    @media (min-width: 1200px) {
        padding: 140px 0;
    }
    
    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        h2 {
            margin: 0;
            color: $black;
            font-family: $font-family-denim;
            font-weight: 400;
        }
        
        @media (max-width: 1199px) {
            .large-button {
                display: none;
            }
        }
    }
}
