.rsl-meet-partners {
    padding: 140px 0;
    position: relative;
    
    &__title {
        color: $black;
        margin: 0;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 440px;
    }
    
    &__wrap-logos {
        display: flex;
        flex-wrap: wrap;
        gap: 40px;
        padding-top: 80px;
        justify-content: center;
        
        @media (min-width: 992px) {
            justify-content: flex-start;
        }
    }
    
    &__logo-item {
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
        
        @media (min-width: 576px) {
            max-width: 176px;
        }
        
        svg {
            max-width: 100%;
            height: auto;
        }
    }
    
}
