.rsl-request-demo {
    position: relative;
    padding: 140px 0;
    
    &__title {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 650px;
    
        h1 {
            color: $black;
            margin: 0;
            font-family: $font-family-denim;
        }
        
        p {
            margin: 0;
            font-size: math-clamp(16, 22);
            line-height: 150%;
            color: $gray-600;
            padding-top: 40px;
        }
    }
    
    &__content {
        position: relative;
        padding: 60px 0;
        margin-top: 80px;
        
        @media (min-width: 992px) {
            padding: 120px 0;
        }
        
        
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            border-radius: 0 0 16px 0;
            z-index: -10;
            background-color: #fff;
            
            @media (min-width: 1400px) {
                width: calc(100% - 128px);
            }
        }
    }
    
    &__form {
        display: flex;
        flex-wrap: wrap;
        gap: 24px;
        max-width: 1128px;
        width: 100%;
        justify-content: center;
        margin-bottom: 20px;
        
        @media (min-width: 1200px) {
            justify-content: flex-start;
        }
    }
    
    &__form-wrap {
        position: relative;
        flex: 0 0 auto;
        width: 100%;
        max-width: 100%;
    
        @media (min-width: 992px) {
            max-width: 360px;
        }
        
        input:focus + label {
            top: 10px;
            left: 24px;
            font-size: 12px;
        }
        
        input:focus,
        input:active {
            padding-left: 24px;
            padding-top: 22px;
            padding-bottom: 10px;
        }
        
        input {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
            width: 100%;
            border-radius: 60px;
            border: 1px solid $black;
            background-color: $gray-100;
            padding: 16px 24px;
            color: $black;
            font-size: 16px;
            line-height: 24px;
            
            @media (min-width: 992px) {
                max-width: 360px;
            }
        }
        
        label {
            position: absolute;
            color: $gray-600;
            font-size: 16px;
            left: 24px;
            top: 16px;
            transition: 0.5s all;
        }
    }
    
    &__wrap-textarea {
        width: 100%;
        height: auto;
        position: relative;
        
        textarea {
            width: 100%;
            resize: none;
            border-radius: 30px;
            border: 1px solid $black;
            background-color: $gray-100;
            height: 270px;
            padding: 18px 24px;
            color: $black;
            font-size: 16px;
            line-height: 24px;
            
            &:focus {
                outline: none;
            }
        }
        
        label {
            position: absolute;
            top: 16px;
            left: 24px;
            color: #61665d;
            font-size: 16px;
            transition: .5s all;
        }
        
        textarea:focus + label {
            top: 8px;
            left: 24px;
            font-size: 12px;
        }
    }
}
