.large-button {
    color: $black;
    background-color: $light-green;
    font-size: 14px;
    line-height: 14px;
    font-family: $font-family-denim;
    font-weight: 400;
    padding: 18px 32px;
    border: 1px solid $black;
    border-radius: 50px;
    transition: 0.5s all;
    width: max-content;
    height: max-content;
    
    &:hover {
        background-color: $orange;
    }
}

.small-button {
    color: $black;
    background-color: $light-green;
    font-size: 14px;
    line-height: 14px;
    font-family: $font-family-denim;
    font-weight: 400;
    padding: 14px 18px;
    border: 1px solid $black;
    border-radius: 50px;
    transition: 0.5s all;
    width: max-content;
    height: max-content;
    
    &:hover {
        background-color: $orange;
    }
}

.btn-mobile  {
    width: 100%;
    margin-top: 40px;
    
    @media (min-width: 1200px) {
        display: none;
    }
}

.btn-width {
    
    @media (max-width: 767px) {
        width: 100%;
    }
}


