.rsl-our-values {
    position: relative;
    padding: 60px 0;
    
    @media (min-width: 992px) {
        padding: 140px 0;
    }
    
    &__title {
        
        
        h2 {
            color: $black;
            margin: 0;
            font-family: $font-family-denim;
        }
        
        p {
            color: $gray-600;
            margin: 0;
            font-size: math-clamp(16, 18);
            line-height: 160%;
            padding-top: math-clamp(20, 40);
        }
    }
    
    &__cards-wrap {
        display: flex;
        gap: 40px;
        padding-top: 40px;
        flex-wrap: wrap;
        flex-direction: column;
        
        @media (min-width: 992px) {
            flex-direction: row;
            justify-content: flex-start;
            padding-top: 80px;
        }
    }
    
    &__card-item {
        flex: 0 0 100%;
        max-width: 284px;
        background-color: $white;
        border-radius: 8px;
        padding: 48px 40px;
        display: flex;
        flex-direction: column;
        row-gap: 32px;
        
        @media (max-width: 991px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
        }
    }
    
    &__icon {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        border: 1px solid $black;
        background-color: $light-green;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    &__info {
        display: flex;
        flex-direction: column;
        row-gap: 24px;
        
        h5 {
            margin: 0;
            color: $black;
            font-family: $font-family-denim;
        }
        
        p {
            margin: 0;
            color: $gray-600;
            font-size: 14px;
            line-height: 22px;
        }
    }
}
