.rsl-support-brokers {
    padding: 60px 0;
    position: relative;
    
    @media (min-width: 992px) {
        padding: 140px 0;
    }
    
    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: -10;
        background-color: #fff;
        
        @media (min-width: 1400px) {
            width: calc(100% - 128px);
            border-radius: 0 16px 16px 0;
        }
    }
    
    &__title {
        color: $black;
        margin: 0;
        text-align: center;
    }
    
    &__items-wrap {
        display: flex;
        column-gap: 40px;
        padding-top: 34px;
        flex-direction: column;
        align-items: center;
        row-gap: 40px;
        
        @media (min-width: 1200px) {
            flex-direction: row;
            align-items: flex-start;
        }
    }
    
    &__item {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        row-gap: 32px;
        
        @media (min-width: 576px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 348px;
            width: 100%;
            display: flex;
            flex-direction: column;
            row-gap: 32px;
        }
        
        svg {
            max-width: 100%;
        }
    }
    
    &__info {
        
        h5 {
            color: $black;
            margin: 0;
        }
        
        p {
            font-size: 14px;
            line-height: 22px;
            color: $gray-600;
            margin: 0;
            padding-top: 24px;
        }
    }
}
