.rsl-why-header-preview {
    padding: 140px 0 66px 0;
    
    .large-button {
        
        @media (max-height: 767px) {
            width: 100%;
        }
    }
    
    &__desc {
        margin: 0;
        color: $gray-600;
        font-size: math-clamp(16, 22);
        line-height: 150%;
        padding: 40px 0 48px 0;
        max-width: 822px;
    }
    
    &__statistic {
        display: flex;
        margin-top: 56px;
        flex-direction: column;
        row-gap: 40px;
        
        @media (min-width: 992px) {
            margin-top: 160px;
            flex-direction: row;
        }
    }
    
    &__statistic-item {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        text-align: center;
        
        @media (min-width: 992px) {
            text-align: left;
        }
        
        span {
            font-size: 72px;
            color: $black;
            font-weight: 400;
            font-family: $font-family-denim;
            line-height: 77px;
            letter-spacing: -0.025em;
            text-transform: capitalize;
        }
        
        p {
            margin: 0;
            font-size: 16px;
            line-height: 24px;
            color: $gray-600;
        }
    }
}
