.rsl-contact-thank-you {
    position: relative;
    padding: 140px 0;
    
    &__title {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 770px;
        row-gap: 40px;
        width: 100%;
        
        h1 {
            color: $black;
            margin: 0;
            font-family: $font-family-denim;
            padding-bottom: 40px;
        }
        
        p {
            font-size: math-clamp(16, 22);
            line-height: 150%;
            color: $gray-600;
            margin: 0;
        }
        
        span {
            text-decoration: underline;
        }
    }
    
    &__wrap {
        margin-top: 80px;
        padding: 60px 0 50px 0;
        position: relative;
        z-index: 10;
        
        @media (min-width: (992px)) {
            padding: 125px 0 100px 0;
        }
    
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: -10;
            background-color: $white;
            
            @media (min-width: 1200px) {
                width: calc(100% - 128px);
                border-radius: 0 0 16px 0;
            }
        }
    }
    
    &__title-form {
        display: flex;
        flex-direction: column;
        row-gap: 40px;
        max-width: 870px;
    
        h1 {
            color: $black;
            margin: 0;
            font-family: $font-family-denim;
        }
    
        p {
            font-size: math-clamp(16, 22);
            line-height: 150%;
            color: $gray-600;
        }
    }
}
