.rsl-company-preview {
    padding: 140px 0 60px 0;
    
    @media (min-width: 992px) {
        padding: 140px 0;
    }
    
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        row-gap: 24px;
        
        @media (min-width: 992px) {
            flex-direction: row;
            column-gap: 100px;
        }
    }
    
    &__title {
        display: flex;
        flex-direction: column;
        row-gap: math-clamp(24, 40);
        height: auto;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        
        @media (min-width: 992px) {
            max-width: 500px;
        }
        
        
        h1 {
            margin: 0;
            font-family: $font-family-denim;
            color: $black;
        }
        
        p {
            color: $gray-600;
            margin: 0;
            font-size: math-clamp(16, 22);
            line-height: 150%;
        }
    }
    
    &__image {
        display: flex;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
        width: 100%;
        height: auto;
        padding: 16px;
        background-color: $white;
        border-radius: 16px;
        box-shadow: 16px 16px 40px rgba(0, 0, 0, 0.04);
        
        @media (min-width: 992px) {
            max-width: 608px;
        }
        
        justify-content: center;
        align-items: center;
        
        img {
            width: 100%;
            height: auto;
        }
    }
}
