.rsl-our-founders {
    background-color: $white;
    padding: 60px 0;
    
    @media (min-width: 992px) {
        padding: 140px 0;
    }
    
    &__wrap {
        display: flex;
        gap: 40px;
        flex-wrap: wrap;
        justify-content: center;
        
        @media (min-width: 992px) {
            justify-content: flex-start;
        }
    }
}
