.rsl-header-preview {
    padding-top: 80px;
    position: relative;
    z-index: 9;
    
    @media (min-width: 1200px) {
        padding-top: 120px;
    }
    
    &__image {
        width: 100%;
        height: math-clamp(294, 600);
        margin-bottom: 48px;
        
        @media (min-width: 1200px) {
            width: calc(50% - 20px);
            max-width: 688px;
            height: 630px;
            box-shadow: 16px 16px 40px rgba(0, 0, 0, 0.04);
            border-radius: 16px 0 0 16px;
            background-color: $white;
            padding: 20px 0 20px 20px;
            position: absolute;
            top: 120px;
            right: 0;
            margin-bottom: 0;
        }
        
        &-wrap {
            height: 100%;
            
            @media (min-width: 1200px) {
                background-color: $gray-100;
                box-shadow: inset 4px 4px 16px rgba(0, 0, 0, 0.02);
                border-radius: 12px 0 0 12px;
            }
            
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    
    &__container {
        @extend .rsl-container;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
    }
    
    &__desc {
        flex: 0 0 100%;
        max-width: 100%;
        display: flex;
        align-items: center;
    
        @media (min-width: 1200px) {
            flex: 0 0 calc(50% - 20px);
            max-width: 610px;
            height: math-clamp(400, 630);
        }
    
        div {
            display: flex;
            flex-direction: column;
            row-gap: math-clamp(30, 40);
            align-items: flex-start;
        }
        
        h1 {
            font-family: $font-family-denim;
            font-weight: 500;
            margin: 0;
            color: $black;
        }
        
        p {
            color: $gray-600;
            margin: 0;
            font-weight: 400;
            line-height: 150%;
        }
    }
}
